    .scroll-button {
      position: fixed;
      right: 20px;
      bottom: 20px;
      width: 60px;
      height: 60px;
      background-color: #28a745;
      /*    */
      color: white;
      border: none;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
      z-index: 1000;
      opacity: 0;
      visibility: hidden;
      transform: translateY(20px);
      transition: opacity 0.4s ease, transform 0.4s ease, visibility 0.4s, background-color 0.4s ease;


      &.visible {
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
        animation: float 2s ease-in-out infinite;
      }

      /* SVG  */
      & svg {
        width: 24px;
        height: 24px;
        fill: white;
      }
    }

    @keyframes float {
      0% {
        transform: translateY(0);
      }

      50% {
        transform: translateY(-6px);
      }

      100% {
        transform: translateY(0);
      }
    }

    /*    */
    /*   */
    @keyframes bounce {
      0% {
        transform: scale(1) translateY(0);
      }

      20% {
        transform: scale(1.2) translateY(-20px);
      }

      50% {
        transform: scale(0.9) translateY(10px);
      }

      70% {
        transform: scale(1.05) translateY(-5px);
      }

      100% {
        transform: scale(1) translateY(0);
      }
    }

    .bounce {
      animation: bounce 0.6s ease forwards;
    }