.breadcrumbs {
  padding: rem(20) 0;

  &__item {
    display: inline-block;
    font-size: rem(12);
    padding-right: rem(14);
    margin-right: rem(8);
    position: relative;

    &::after {
      position: absolute;
      content: '';
      background-image: url(../images/icons/icon-right.svg);
      width: rem(6);
      height: rem(6);
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }

    &:last-child {
      padding-right: 0;
      margin-right: 0;
    }

    &:last-child::after {
      display: none;
      padding-right: 0;
      margin-right: 0;
    }
  }

  &__nolink {
    color: var(--gray-bright);
  }

  // @media screen and (width < 980.01px) {
  //   &__inner {
  //     overflow-x: scroll;
  //   }
  // }

}